 * @Version: V1.0
 * @Date: 2022-08-25 15:42:13
 * @LastEditTime: 2024-03-25 22:47:32
 * @Description:
 * @Others: None
-->
<template>
  <div>

    <div class="konglong-content">

      <el-progress :stroke-width="40" :percentage="percentage" color="#f3e993" :format="format"
        style="width: 900px;padding-bottom: 10px;margin-top: 30px;"></el-progress>
      <div class="konglong-box">
        <div class="konglong1" :class="classObj" @click="changeKonglongNow"></div>
        <div class="cloud" v-for="(item, index) in cloudData" :key="index"
          :class="{ cloud_xiaoshi: item.show_cloud_disappear, cloud_swing: (((cloudData.length - 1 + 4 - index) - nowIndex) < 5 && ((cloudData.length - 1 + 4 - index) - nowIndex) > 0) && !item.show_cloud_disappear }"
          :style="{ opacity: (((cloudData.length - 1 + 4 - index) - nowIndex) < 5 && ((cloudData.length - 1 + 4 - index) - nowIndex) > 0) ? '1' : 0, zIndex: (cloudData.length - index), top: item.y + 'px', left: item.x + 'px' }">
          <div v-if="item.show_baojian" class="baojian baojian_huadong" :class="{ baojian_huadong: item.show_baojian }">
          </div>
        </div>
        <div class="love" v-show="show_love">
          <span> +{{ pointNum }}</span>
        </div>
      </div>
      <div>

        <div class="date-box" style="bottom:120px"> Time is tight!</div>
        <div class="date-box"> {{ countdown.countdownStr }}</div>
      </div>

    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
export default {
  components: {},
  props: {},
  data() {
    return {
      show_love: false,
      percentage: 0,
      cloudData: [],
      nowIndex: 0,
      perentageeIndex: 0,
      perecentageMax: window.$setting.allPoint,
      pointNum: 0,
      countdown: {
        endTime: dayjs().valueOf() + (window.$setting.countdown * 3600000),
        countdownStr: ''
      },
      classObj:''
    };
  },
  watch: {},
  computed: {},
  methods: {
    format(percentage) {
      let percentageText = window.$setting.percentage0 + (percentage > 0 ? percentage : '');
      if (percentage > window.$setting.percentage1Num) {
        percentageText = window.$setting.percentage1 + percentage
      }
      if (percentage > window.$setting.percentage2Num) {
        percentageText = window.$setting.percentage2 + percentage
      }
      if (percentage > window.$setting.percentage3Num) {
        percentageText = window.$setting.percentage3 + percentage
      }
      return percentage === window.$setting.percentage4Num ? window.$setting.percentage4  : `${percentageText} `;

    },

    //创建数据
    createdCloudData() {
      let num = 0;
      let order = 0;
      let caseList = [
        {
          x: 55,
          y: 43,
        }, {
          x: 3,
          y: 220,
        }, {
          x: 320,
          y: 20,
        }, {
          x: 540,
          y: 120,
        }, {
          x: 620,
          y: 320,
        }
      ];
      while (num < this.perecentageMax) {
        let randomNum = Math.floor(Math.random() * 4) + 1 //0-4随机数 
        if ((num + randomNum) > this.perecentageMax) {
          randomNum = this.perecentageMax - num;
        }
        num += randomNum;
        order += 1;
        if (order === 4) {
          order = 0;
        }
        let caseData = {
          x: caseList[order].x,
          y: caseList[order].y,
          pointNum: randomNum,
          show_cloud_disappear: false,
          show_cloud: true,
          show_baojian: false,
          orderType: order,
        };
        this.cloudData.push(caseData)
      }
      console.log(this.cloudData)
      this.nowIndex = this.cloudData.length - 1;
      this.perentageeIndex = this.cloudData.length - 1;

    },
    changeKonglongNow() {
      
      if (this.perentageeIndex === -1) {
        return
      }
      this.show_love = true;
      this.percentage += this.cloudData[this.perentageeIndex].pointNum;
      this.cloudData[this.cloudData.length - 1 - this.nowIndex].show_cloud_disappear =
        !this.cloudData[this.cloudData.length - 1 - this.nowIndex].show_cloud_disappear;
      this.cloudData[this.cloudData.length - 1 - this.nowIndex].show_baojian =
        !this.cloudData[this.cloudData.length - 1 - this.nowIndex].show_baojian;
      this.perentageeIndex -= 1;
      this.pointNum = this.cloudData[this.nowIndex].pointNum;

      //计算小龙样式
      console.log(this.cloudData.length);
      console.log(this.perentageeIndex);     
      if(this.perentageeIndex <= 0){
        this.classObj = 'konglong5'
      }
      if(this.perentageeIndex > 0.1*this.cloudData.length){
        this.classObj = 'konglong4'
      }
      if(this.perentageeIndex > 0.25*this.cloudData.length){
        this.classObj = 'konglong3'
      }
      if(this.perentageeIndex > 0.5*this.cloudData.length){
        this.classObj = 'konglong2'
      }

      if(this.perentageeIndex > 0.75*this.cloudData.length){
        this.classObj = 'konglong1'
      }
     
     
     
      

      setTimeout(() => {
        this.nowIndex -= 1;
        this.show_love = false;
      }, 3000)
      //console.log(this.nowIndex)
    },
    updateCountdownStr(countdown) {
      const now = dayjs().valueOf();
      const diff = countdown.endTime - now;
      //console.log(diff)
      if (diff < 0) {
        countdown.countdownStr = window.$setting.countdownText
      } else {
        const h = dayjs(diff).format('HH') - 8;
        const m = dayjs(diff).format('mm')
        const s = dayjs(diff).format('ss')
        countdown.countdownStr = `${h}:${m}:${s}`
      }
    }
  },

  beforeCreate() { },
  created() { },
  mounted() {
    console.log(window.$setting)
    document.title = "拯救小恐龙";
    this.createdCloudData();
    setInterval(() => {
      this.updateCountdownStr(this.countdown)
    }, 1000)
    const now = dayjs().valueOf();
  }
};
</script>

<style lang="scss" scoped>
.konglong-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  //justify-content: center;
  background: url("./../../static/images/caoping_bg.jpg") no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100vh;
  cursor: pointer;
}

.konglong-box {
  position: relative;
  width: 720px;
  text-align: center;
  display: flex;
  justify-content: center;
  padding-top: calc(50vh - 200px);
}


.konglong1 {
  //background: url("./../../static/images/konglong_bg2.png");
  background: url("./../assets/css/konglong/1.png");
  width: 600px;
  height: 700px;
  background-size: 100% 100%;
  cursor: pointer;
  margin-top: -150px;
}
.konglong2 {
  //background: url("./../../static/images/konglong_bg2.png");
  background: url("./../assets/css/konglong/2.png");
  width: 600px;
  height: 700px;
  background-size: 100% 100%;
  cursor: pointer;
  margin-top: -150px;
}
.konglong3{
  //background: url("./../../static/images/konglong_bg2.png");
  background: url("./../assets/css/konglong/3.png");
  width: 600px;
  height: 700px;
  background-size: 100% 100%;
  cursor: pointer;
  margin-top: -150px;
}
.konglong4{
  //background: url("./../../static/images/konglong_bg2.png");
  background: url("./../assets/css/konglong/4.png");
  width: 600px;
  height: 700px;
  background-size: 100% 100%;
  cursor: pointer;
  margin-top: -150px;
}

.konglong5 {
  //background: url("./../../static/images/konglong_bg2.png");
  background: url("./../assets/css/konglong/5.png");
  width: 600px;
  height: 700px;
  background-size: 100% 100%;
  cursor: pointer;
  margin-top: -150px;
}

.baojian {
  background: url("./../../static/images/baojian_bg.png");
  width: 100px;
  height: 100px;
  background-size: 100% 100%;
  position: absolute;
  left: calc(50% - 50px);
  top: calc(50% - 50px);
  //opacity: 1;
  z-index: 600;
}

// 宝剑滑动
.baojian_huadong {
  //animation: infinite;
  animation-fill-mode: forwards;
  animation-name: baojian_disappear;
  animation-duration: 1s;

}

.cloud_swing {
  animation: infinite;
  animation-name: cloud_swing;
  animation-duration: 2s;
}

.cloud {
  background: url("./../../static/images/yun_bg2.png");
  width: 180px;
  height: 100px;
  background-size: 100% 100%;
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;

}

// 云消失
.cloud_xiaoshi {
  animation-name: cloud_disappear;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

// 云飘动
.showy-box {
  animation: infinite;
  animation-name: cloud_showy;
  animation-duration: 1s;
}

.love {
  background: url("./../../static/images/love_bg.png");
  width: 180px;
  height: 72px;
  position: absolute;
  left: calc(50% - 22px);
  bottom: 50px;
  background-size: 100% 100%;
  z-index: 300;
  animation: infinite;
  animation-name: love_float;
  animation-duration: 3s;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
  font-weight: 600;
  font-size: 20px;
}

/* 动画代码 爱心浮动*/
@keyframes love_float {
  0% {
    bottom: 100px;
    opacity: 1;
    left: calc(50% + 22px);
  }

  20% {
    opacity: 1;
    left: calc(50% - 22px);
  }

  40% {
    opacity: 1;
    left: calc(50% + 22px);
  }

  60% {
    opacity: 1;
    left: calc(50% - 22px);
  }

  80% {
    left: calc(50% + 22px);
  }

  100% {
    left: calc(50% - 22px);
    bottom: 300px;
    opacity: 0;
  }
}


/* 动画代码 云消失*/
@keyframes cloud_disappear {
  0% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}


/* 动画代码 宝剑消失 飘动*/
@keyframes baojian_disappear {
  0% {
    opacity: 1;
    transform: rotate(30deg);
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(-20deg);
  }
}


/* 动画代码 云摇晃*/
@keyframes cloud_swing {
  0% {
    transform: rotate(10deg);
  }

  50% {
    transform: rotate(-10deg);
  }

  100% {
    transform: rotate(10deg);
  }
}

/* 动画代码 飘动*/
@keyframes cloud_showy {
  from {
    margin-top: 0px;
    margin-left: 0px;
  }

  to {
    margin-top: 5px;
    margin-left: 5px;
  }
}

.date-box {
  position: absolute;
  bottom: 60px;
  left: 60px;
  font-size: 40px;
  font-weight: 600;
  color: #ff8d00;
}
</style>
<style>
body {
  margin: 0;
  padding: 0;
}

.el-progress__text {
  padding-top: 10px !important;
  margin: 0 auto;
  display: block !important;
  text-align: center !important;
  font-size: 30px;
  font-weight: 600;
}

.el-progress-bar__outer {
  box-shadow: 0 0 2px #ffa500, 0 0 0px #ffa500, 0 0 10px #ffa500, 0 0 20px #ffa500, 0 0 10px #ff8d00;
}
</style>



